@tailwind base;
@tailwind components;
@tailwind utilities;


/* Styling for WebKit browsers like Chrome, Safari */
::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-track {
    background: #2e2e2e; /* Dark track */
}

::-webkit-scrollbar-thumb {
    background: #555; /* Darker thumb */
}

::-webkit-scrollbar-thumb:hover {
    background: #444; /* Even darker thumb on hover */
}

/* Styling for Firefox */
html {
    scrollbar-width: thin;
    scrollbar-color: #555 #2e2e2e; /* thumb and track color */
}
